import React, { useContext } from 'react';
/** @jsxImportSource @emotion/react */
import {css} from '@emotion/react';
import { AppTheme } from '../App';

interface Props {
    text: string
    color?: string
}

function TagBox({text, color}: Props){
    const appTheme = useContext(AppTheme);

    const style = css`
        display: flex;
        align-items: center;
        padding: 2px 10px;
        width: fit-content;
        border-radius: var(--radius);
        font-size: 16px;
        font-weight: 500;  
        white-space: nowrap;

        ${appTheme === 'dark' ? `
            background-color: #545454;
            color: white;
            ` : `
            background-color: #545454;
            color: white;
        `}

        ${color ? `
            background-color: ${color};
        ` : ''}

        margin: 5px 15px 5px 0;
        transition: .3s;
    
        @media screen and (max-width: 420px){
            font-size: 14px;
            padding: 2px 5px;
        }
    `;

    return (
        <div css={style}>
            {text}
        </div>
    );
}


export default TagBox;