import React, { useContext, useRef, useState } from 'react';
/** @jsxImportSource @emotion/react */
import {css} from '@emotion/react';
import {TagBox} from './index';
import {SiGithub} from 'react-icons/si';
import {HiOutlineExternalLink} from 'react-icons/hi';
import { AppTheme } from '../App';
import { fadeInMixin } from '../style/mixin';

interface Props {
    imgUrl: string
    projectName: string
    period: string
    description: string
    techList: string[]
    link: string
    site: string
    imgDesc?: string

    coworkTool?: string[]
    extraLink?: {
        url: string
        desc: string 
    }[]
    members?: {
        count: number
        desc: string
        contribution: string
    }
}

function ProjectBlock({imgUrl, projectName, period, description, techList, link, site, imgDesc='', coworkTool, extraLink, members}: Props){
    const appTheme = useContext(AppTheme);

    return (
        <div css={style(appTheme)} data-fade-in>
            <div className='wrapper'>
                <div className='preview'>
                    <img src={imgUrl} alt={imgDesc}></img>
                </div>
                <div className='explain'>
                    <div className='title-box'>
                        <h3 className='tit'>
                            {projectName}
                        </h3>
                        <span>{period}</span>
                    </div>
                    {members && 
                        <div className='team'>
                            <div className='count'>
                                팀 인원<span>{members.count}명 &nbsp; ({members.desc})</span>
                            </div>
                            <div className='contribution'>
                                내 역할<span>{members.contribution}</span>
                            </div>
                        </div>
                    }
                    <div className='desc'>
                        {description}
                    </div>
                    <div className='tech'>
                        {techList.map(t => (
                            <TagBox key={t} text={t}></TagBox>
                        ))}
                    </div>
                    {coworkTool && 
                        <div className='tech'>
                            {coworkTool.map(t => (
                                <TagBox key={t} text={t} color={'#6262d9'}></TagBox>
                            ))}
                        </div>
                    }
                    <div className='link'>
                        <a title='깃허브 링크' target='_blank' href={link}>
                            <SiGithub size='45' data-name='git'/>
                        </a>
                        {site && 
                            <a title='페이지 링크' target='_blank' href={site}>
                                <HiOutlineExternalLink size='48' data-name='site'/>
                            </a>
                        }
                    </div>
                    {extraLink && 
                        extraLink.map((extra, i) => (
                            <div className='extra' key={i}>
                                <span className='desc'>{extra.desc}</span>
                                <div className='wrap'>
                                    <a className='url' target='_blank' title={'링크'} href={extra.url}>{extra.url}</a>
                                </div>
                            </div>
                        ))
                    }
                </div>
            </div>
        </div>
    );
}

const style = (theme: string)=>(css`
    padding: 20px 0;
    margin-bottom: 100px;
    ${fadeInMixin}
    .wrapper {
        display: flex;
        transition: 1s;
        .preview {
            position: relative;
            width: 550px;
            min-width: 550px;
            height: 400px;
            /* padding: 40px; */
            overflow: hidden;
            border-radius: var(--radius);
            transition: .5s;

            ${theme === 'dark' ? `
                box-shadow: 0 0 5px 0px gray;
            ` : `
                box-shadow: 0 0 30px -5px black;
            `}
            
            img {
                z-index: 1;
                position: relative;
                width: 100%;
                height: 100%;
                border-radius: 10px;
                box-shadow: 0 0 15px -3px black;
                transition: .3s;
                &:hover {
                    transform: scale(1.1);
                }
            }
            [class*='cir'] {
                position: absolute;
                border-radius: 50%;
                transition: .5s;
                filter: grayscale(1);
            }
            .cir1 {
                width: 150px;
                height: 150px;
                top: -50px;
                left: -50px;
                background-color: #bfc5c4;
            }
            .cir2 {
                width: 110px;
                height: 110px;
                top: 300px;
                left: 400px;
                background-color: #8affe4;
            }
            .cir3 {
                width: 100px;
                height: 100px;
                top: 300px;
                left: -10px;
                background-color: #9ed5b2;
            }
            .cir4 {
                width: 70px;
                height: 70px;
                top: 0;
                left: 410px;
                background-color: #80ffad;
            }
        }

        .title-box {
            display: flex;
            .tit {
                color: ${theme === 'dark' ? 'var(--color-main)' : '#545454'};
                font-weight: 600;
                font-size: 30px;
                margin-bottom: 20px;
            }
            span {
                margin: 15px 0 0 25px;
                ${theme === 'dark' ? `
                    color: #b7b7b7;
                ` : `
                    color: #8d8d8d;
                `}
            }
        }

        .explain {
            padding-left: 40px;
            flex-grow: 1;
            color: ${theme === 'dark' ? 'white' : 'black'};
            transition: 1s;
            .for {
                font-size: 20px;
                margin: 6px 0;
            }
            .team {
                margin-bottom: 15px;
                line-height: 30px;
                font-weight: bold;
                ${theme === 'dark' ? `
                    color: #b7b7b7;
                ` : `
                    color: #8d8d8d;
                `}
                span {
                    &::before {
                        content: '-';
                        margin: 0 10px;
                    }
                } 
            }
            > .desc {
                white-space: pre-line;
                font-size: 18px;
                line-height: 30px;
                margin-bottom: 10px;
            }
            .tech {
                display: flex;
                flex-wrap: wrap;
                margin-top: 7px;
            }
            .link {
                margin: 15px 0 0 15px;
                a {
                    color: unset;
                }
                svg {
                    margin-right: 15px;
                    transition: .3s;
                    &:hover {
                        cursor: pointer;
                        transform: scale(1.1);
                    }
                }
            }
            .extra {
                display: flex;
                margin-top: 15px;
                font-weight: bold;
                .desc {
                    &::after {
                        content: '-';
                        margin: 0 10px;
                    }
                }
                .wrap {
                    max-width: 450px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                }
            }
        }
    }

    @media screen and (max-width: 1400px) {
        .wrapper {
            flex-direction: column;
            align-items: center;
            .explain {
                flex-grow: unset;
                width: 700px;
                padding-left: 80px;
                padding-top: 15px;
            }
        }
    }

    @media screen and (max-width: 710px) {
        .wrapper {
            .preview {
                width: 460px;
                min-width: 460px;
                height: 300px;
            }
            .explain {
                width: 450px;
                padding-left: 0;
                .tech {
                    width: calc(100% - 30px);
                }
                .extra {
                    .wrap {
                        max-width: 300px;
                    }
                }
            }
        }
    }

    @media screen and (max-width: 420px){
        .wrapper {
            .preview {
                width: 100%;
                min-width: unset;
                height: 280px;
            }
            .explain {
                width: 100%;
                min-width: unset;
                .tit {
                    font-size: 30px;
                }
                .link {
                    svg {
                        width: 40px;
                    }
                }
                .tech {
                    
                }
            }
        }
    }
`);

export default ProjectBlock;